.custom-select {
    position: relative;
    display: inline-block;
}

.custom-select select {
    display: none;
}

.select-arrow {
    position: absolute;
    right: 10px;
    top: calc(50% - 4px);
    width: 0;
    height: 0;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-top: 4px solid #000; /* Mu sc ca mi tn */
}

/* Ty chnh giao din ca dropdown */
.custom-select select {
    appearance: none; /* Loi b giao din mc nh ca dropdown */
    -webkit-appearance: none;
    -moz-appearance: none;
    padding: 8px 16px; /* Ty chnh padding */
    border: 1px solid #ccc; /* Ty chnh border */
    border-radius: 4px; /* Ty chnh gc bo */
    background-color: #fff; /* Ty chnh mu nn */
    cursor: pointer;
}

/* Khi dropdown c focus */
.custom-select select:focus {
    outline: none;
    border-color: #000; /* Ty chnh mu border khi focus */
}

/* Khi dropdown c hover */
.custom-select select:hover {
    background-color: #f9f9f9; /* Ty chnh mu nn khi hover */
}
